@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.QueryHistory {
  height: 300px;
  overflow: scroll;
}

.QueryHistory .mdc-list-item {
  display: flex;
}

.QueryHistory .query {
  height: 2em;
  font-family: monospace;
  white-space: nowrap;
  flex: 1 1;
}

.QueryHistory .time {
  flex-shrink: 0;
}

.QueryHistory .language,
.QueryHistory .status {
  margin: 0 16px;
}

main {
  display: flex;
  flex-direction: column;
}

.graph {
  width: 100%;
  flex: 1 1;
}

.graph svg {
  height: 100%;
  width: 100%;
}

.App {
  height: 100vh;
  width: 100vw;
  background: #eee;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}

.App .Logo {
  height: 2rem;
  margin-right: 8px;
  position: relative;
  top: 0.5rem;
  display: inline-block;
}

.App > main {
  flex: 1 1;
  overflow: hidden;
  height: 100%;
  box-sizing: border-box;
}

.App > main .mdc-typography--headline6 {
  margin: 0 30px;
  padding: 12px 0;
  display: block;
}

.App > main .actions {
  background: white;
  padding: 8px 24px;
  border-color: #d3d3d3;
  border-style: solid;
  border-top-width: 1px;
  border-bottom-width: 1px;
  border-left-width: 0;
  border-right-width: 0;
}

.App main .actions > .mdc-button {
  margin-right: 24px;
}

.App main .actions > .mdc-button:not(:first-child) {
  margin-left: 24px;
}

